import { Navigate, useNavigate } from "react-router-dom";
import { useRecoilState, useResetRecoilState } from "recoil";

import { postLogin } from "../api/signUpApi";
import { removeCookie, setCookie } from "../util/cookieUtil";
import { atomSignState } from "../atom/loginState";

const useCustomLogin = () => {
  //  
  const navigate = useNavigate();

  // Recoil  Atom 
  const [loginState, setLoginState] = useRecoilState(atomSignState);

  // Recoil  atom 
  const resetSignState = useResetRecoilState(atomSignState);

  // const isLogin = loginState && loginState.email ? true : false;
  const isLogin =
    loginState && Object.keys(loginState).length > 0 ? true : false;

  //  
  // const doLogin = async ({ loginParam, successFn, failFn, errorFn }) => {
  //   const result = await postLogin({ loginParam, successFn, failFn, errorFn });
  //   saveAsCookie(result);
  //   moveToPath("/");

  //   return result;
  // };

  const doLogin = async ({ loginParam }) => {
    const result = await postLogin({ loginParam });
    saveAsCookie(result);
    window.scroll(0,0)
    moveToPath("/");
    // navigate("/main");

    return result;
  };
  const saveAsCookie = result => {
    setLoginState(result);

    setCookie("member", JSON.stringify(result), 1);
  };
  // console.log("  :", loginState, isLogin);

  //  
  const doLogout = () => {
    removeCookie("member");
    resetSignState();
    const Rest_api_key = "94b443da7db84c565579d43ba563dd3f";
    const LOGOUT_REDIRECT_URI = "http://localhost:3000/sign/kakao/logout";

    const kakaoURLout = `https://kauth.kakao.com/oauth/logout?client_id=${Rest_api_key}&logout_redirect_uri=${LOGOUT_REDIRECT_URI}`;

    const handleLogout = () => {
      window.location.href = kakaoURLout;
    };
    handleLogout();
    // moveToPath("/");
    //     
    // window.location.reload();
  };

  //  
  const moveToPath = path => {
    navigate({ pathname: path }, { replace: true });
  };

  //   
  const moveToLogin = () => {
    console.log(" ");
    return <Navigate replace to="/login" />;
  };

  return {
    loginState,
    isLogin,
    doLogin,
    doLogout,
    moveToPath,
    moveToLogin,
    saveAsCookie,
  };
};

export default useCustomLogin;
